import { Dispatch } from "react";
import { actionTypes } from "../constants";
import { interfaceActionUser, loginInterface, userInterface } from "../interface";


export const actionCadastrar = (user: userInterface) => {
    return ((dispatch: Dispatch<interfaceActionUser>) => {
        dispatch({
            type: actionTypes.CADASTRAR,
            payload: user
        })
    })
}


export const loginAction = (user: loginInterface) => {
    return ((dispatch: Dispatch<any>) => {
        dispatch({
            type: actionTypes.LOGIN,
            payload: user,
        })
    })

};