import React from 'react'
//img
import logo from "../../assets/img/logo.png"
//styles
import * as s from "./login.style"
//img
import success from "../../assets/img/success.png"
import warning from "../../assets/img/warning.png"
//packages
import { Link } from "react-router-dom"
import { useDispatch, useSelector } from 'react-redux'
import { bindActionCreators } from 'redux'
import { actionCreators } from '../../state'
import { TypeReducer } from '../../state/combineReducers'

export const Login: React.FC = (): JSX.Element => {

    const [email, setEmail]: [string, (data: string) => void] = React.useState('');
    const [senha, setSenha]: [string, (data: string) => void] = React.useState('');
    const [message, setMessage]: [boolean, (data: boolean) => void] = React.useState<boolean>(false);
    const [messageFill, setMessageFill]: [boolean, (data: boolean) => void] = React.useState<boolean>(false);

    const dispatch = useDispatch()
    const { loginAction } = bindActionCreators(actionCreators, dispatch)

    const state = useSelector((state: TypeReducer) => state.user)

    const handleLogin = (e:any) => {
        e.preventDefault();

        if (email && senha) {
            loginAction(
                {
                    email: email,
                    senha: senha
                }
            )
        } else {
            setMessageFill(!messageFill)
        }
    }


    return (
        <s.Container className="container">

            <div className="wrapper-logo">
                <img src={logo} alt="" />
            </div>

            <div className="wrapper-form">
                <form onSubmit={(e) => handleLogin(e)}>

                    <h1>Login</h1>
                    <hr />

                    <label htmlFor="email">Seu e-mail</label>
                    <input type="email" name="email" id="email" onChange={(e) => setEmail(e.target.value)} />

                    <label htmlFor="senha">Sua senha</label>
                    <input type="password" name="senha" id="senha" onChange={(e) => setSenha(e.target.value)} />

                    <button type="submit">Logar</button>

                    <hr />

                    <div className="wrapper-sign-in">
                        <p>Ainda não tem conta?</p>

                        <Link to="/cadastro">
                            <button type="submit">Cadastrar</button>
                        </Link>
                    </div>
                </form>
            </div>

            {message &&
                <div className="wrapper-message">
                    <img src={success} alt="salvo_com_sucesso" />
                    <p>Usuário cadastrado com sucesso!</p>
                    <Link to="/user">
                        <button onClick={() => setMessage(!message)}>Visualizar</button>
                    </Link>
                </div>
            }

            {messageFill &&
                <div className="wrapper-message">
                    <img src={warning} alt="salvo_com_sucesso" />
                    <p>Verifique se os campos estão preenchidos de forma correta!</p>
                    <button onClick={() => setMessageFill(!messageFill)}>Confirmar</button>
                </div>
            }


        </s.Container>
    )
}
